import { addId, choose, combinationIndex, indexedCombination, StreamSampler } from "./utils";

import { Faction, Building, Unit, Deck, DeckValidationFunction, Request, Response, DeckDB, DeckGenerator } from "./types";
import { SerializedFunction } from "./types";

// Request is immutable
class RequestImpl implements Request {
  readonly faction: Faction | "random";
  readonly name: string;
  readonly validateFn: SerializedFunction<() => DeckValidationFunction>;
  readonly numDecks: number;

  constructor(faction: Faction | "random", name: string, validateFn: SerializedFunction<() => DeckValidationFunction>, numDecks: number) {
    this.faction = faction;
    this.name = name;
    this.validateFn = validateFn;
    this.numDecks = numDecks;
  }
}

// Session describes the run state for each request (private to generator)
// Session is not transferrable (i.e., cannot be sent to other threads)
class Session {
  readonly request: Request;
  readonly validateFn: DeckValidationFunction;
  readonly sampler: StreamSampler<Deck>;
  count: number;

  constructor(request: Request) {
    // console.log(request);
    this.request = request;
    // use static method here since prototype method will be lost after structuredClone
    this.validateFn = SerializedFunction.deserialize(request.validateFn)();
    // this.validateFn = request.validateFn.deserialize()();
    this.sampler = new StreamSampler(request.numDecks);
    this.count = 0;
  }

  // call this method to finalize this session, it will create a Response
  // do NOT attempt to use a session object after it has been finalized
  finalize(): Response {
    return new ResponseImpl(this.request, this.sampler.finalSample(), this.count);
  }
}

// response is immutable
class ResponseImpl implements Response {
  readonly request: Request;
  readonly result: Deck[];
  readonly count: number;

  constructor(request: Request, result: Deck[], count: number) {
    this.request = request;
    this.result = result;
    this.count = count;
  }
}

class DeckImpl implements Deck {
  readonly faction: Faction;
  readonly commandar: Unit;
  readonly units: Unit[];

  constructor(faction: Faction, commandar: Unit, units: Unit[]) {
    this.faction = faction;
    this.commandar = commandar;
    this.units = units;
  }
}

const UNIT_PER_DECK = 6;
// sort by building first, then cost, then name
const BUILDING_ORDER = {"Infantry": 0, "Vehicle": 1, "Air": 2, "Tech": 3, "Commandar": 4};
const ORDER_WITHIN_DECK = (a: Unit, b: Unit): number => {
  if (BUILDING_ORDER[a.building] !== BUILDING_ORDER[b.building]) {
    return BUILDING_ORDER[a.building] - BUILDING_ORDER[b.building];
  } else if (a.cost !== b.cost) {
    return a.cost - b.cost;
  } else {
    return a.name > b.name ? 1 : -1;
  }
}

class DeckDBImpl {
  readonly units: Unit[];
  readonly commandars: Unit[];
  readonly size: number;
  private readonly unitsReverseMap: Map<string, number>;
  private readonly commandarsReverseMap: Map<string, number>;

  constructor(units: Unit[], commandars: Unit[]) {
    // sort by order within deck
    this.units = addId(units.sort(ORDER_WITHIN_DECK));
    // console.log(this.units);
    this.commandars = addId(commandars);
    this.size = choose(units.length, UNIT_PER_DECK) * commandars.length;
    let map = new Map();
    units.forEach((unit, index) => map.set(unit.name, index));
    this.unitsReverseMap = map;
    map = new Map();
    commandars.forEach((unit, index) => map.set(unit.name, index));
    this.commandarsReverseMap = map;
  }

  // get a deck given an index
  get(idx: number): Deck {
    if (idx < 0 || idx >= this.size) {
      throw new Error(`index out of bound: ${idx} is not in range [0, ${this.size})`);
    }

    const unitsIdx = Math.floor(idx / this.commandars.length);
    const commandarIdx = idx % this.commandars.length;

    const resUnits = indexedCombination(this.units.length, UNIT_PER_DECK, unitsIdx).map(d => this.units[d]);
    const resCommandar = this.commandars[commandarIdx];
    return new DeckImpl(resCommandar.faction, resCommandar, resUnits);
  }

  // get the index of a deck
  // TODO: test this method
  getIndex(deck: Deck): number {
    const unitsIdx = deck.units.map(d => this.unitsReverseMap.get(d.name))
                              .filter((d): d is number => typeof d !== undefined);
    const commandarIdx = this.commandarsReverseMap.get(deck.commandar.name);
    if (unitsIdx.length !== UNIT_PER_DECK || commandarIdx === undefined) {
      throw new Error("invalid deck or not part of this database");
    }

    return combinationIndex(unitsIdx, this.units.length) * this.commandars.length + commandarIdx;
  }

  // there seem to be some issue with iterator types
  // TODO: implements iterator
  // [Symbol.iterator](): Iterator<Deck> {
  //   let index: number = 0;
  //   return {
  //     next: () => {
  //       if (index >= this.size) {
  //         return { done: true };
  //       } else {
  //         const idx = index;
  //         index += 1;
  //         return { value: this.get(idx), done: false };
  //       } 
  //     }
  //   }
  // }
}

// TODO: test this
class DeckDBUnion implements DeckDB {
  readonly units: Unit[];
  readonly commandars: Unit[];
  readonly size: number;
  private readonly dbs: DeckDB[];

  // assume each deckDB added has no duplicate units or commandars
  constructor(dbs: DeckDB[]) {
    this.dbs = dbs;
    const totalSize: number = dbs.reduce((acc, db) => { acc += db.size; return acc; }, 0);
    this.size = totalSize;
    let units: Unit[] = [];
    units.concat(...this.dbs.map((db: DeckDB) => db.units));
    this.units = units;
    let commandars: Unit[] = [];
    commandars.concat(...this.dbs.map((db: DeckDB) => db.commandars));
    this.commandars = commandars;
  }

  // lookup deck sequentially in dbs
  get(index: number): Deck {
    if (index < 0 || index >= this.size) {
      throw new Error("index out of bound");
    }
    let tmpIdx = 0;
    while (index >= this.dbs[tmpIdx].size) {
      index -= this.dbs[tmpIdx].size;
      tmpIdx += 1;
    }
    return this.dbs[tmpIdx].get(index);
  }

  getIndex(deck: Deck): number {
    throw new Error("operation not supported");
  }
}

class AbstractDeckGenerator implements DeckGenerator {
  // requests is an array of Requests object
  // deckDB is an DeckDB object
  // assume requests has is using the faction that DeckDB contains
  generateCustom(requests: Request[], deckDB: DeckDB): Response[] {
    if (requests.length > 0) {
      // console.log("generateCustom called");
      // initialize each session
      const sessions: Session[] = requests.map(d => new Session(d));

      // loop over all decks, sampling for each session
      for (let i = 0; i < deckDB.size; i++) {
        const deck = deckDB.get(i);
        sessions.forEach(d => {
          if (d.validateFn(deck)) {
            d.sampler.next(deck);
            d.count += 1;
          }
        })
      }

      return sessions.map(d => d.finalize());
    } else {
      return [];
    }
  }

  generate(request: Request[]): Response[] {
    console.log(`cannot call generate() on abstractDeckGenerator`);
    throw new Error(`cannot call generate() on abstractDeckGenerator`);
  }
}

export { DeckDBImpl, DeckDBUnion, AbstractDeckGenerator };

