import { Faction, Building, Unit, Deck, DeckValidationFunction, Request, Response, DeckDB } from "../model/types";
import { AbstractDeckGenerator, DeckDBImpl, DeckDBUnion } from "../model/model";

// actual implementation of the deck generator, plus a few useful helper methods
class DefaultDeckGenerator extends AbstractDeckGenerator {
  readonly GDI_DECKS: DeckDB;
  readonly NOD_DECKS: DeckDB;
  readonly ALL_DECKS: DeckDB;

  constructor(GDI_UNITS: Unit[], GDI_COMMANDERS: Unit[], NOD_UNITS: Unit[], NOD_COMMANDERS: Unit[]) {
    super();
    this.GDI_DECKS = new DeckDBImpl(GDI_UNITS, GDI_COMMANDERS);
    this.NOD_DECKS = new DeckDBImpl(NOD_UNITS, NOD_COMMANDERS);
    this.ALL_DECKS = new DeckDBUnion([this.GDI_DECKS, this.NOD_DECKS]);
  }

  generate(requests: Request[]): Response[] {
    // save the index first
    const tmp: Map<Request, number> = new Map();
    requests.forEach((r, index) => {
      tmp.set(r, index);
    });

    const gdiRequests: Request[] = requests.filter(r => r.faction === "GDI");
    const nodRequests: Request[] = requests.filter(r => r.faction === "Nod");
    const randomRequests: Request[] = requests.filter(r => r.faction === "random");

    const gdiResponses: Response[] = this.generateCustom(gdiRequests, this.GDI_DECKS);
    const nodResponses: Response[] = this.generateCustom(nodRequests, this.NOD_DECKS);
    const randomResponses: Response[] = this.generateCustom(randomRequests, this.ALL_DECKS);

    let responses: Response[] = [];
    gdiResponses.forEach(r => {
      // this works because r.request is always in the map we created
      responses[tmp.get(r.request) as number] = r;
    });
    nodResponses.forEach(r => {
      responses[tmp.get(r.request) as number] = r;
    });
    randomResponses.forEach(r => {
      responses[tmp.get(r.request) as number] = r;
    });
    return responses;
  }

  generateAsync(requests: Request[], responseFn: (response: Response[]) => void) {
    const responses = this.generate(requests);
    responseFn(responses);
  }

  // convert deck to a string that uniquely identifies it
  getDeckId(deck: Deck): string {
    const faction = deck.faction === "GDI" ? "gdi" : "nod";
    const id = deck.faction === "GDI" ? this.GDI_DECKS.getIndex(deck) : this.NOD_DECKS.getIndex(deck);
    return `${faction}-${id}`;
  }

  // TODO: test this function
  getDeck(deckID: string): Deck {
    const pattern = /^(gdi|nod)-([0-9]+)$/i;
    const res = pattern.exec(deckID);
    if (res === null) {
      throw new Error("invalid deckID");
    } else {
      const faction = res[1];
      const value = parseInt(res[2]);
      if (faction === "gdi") {
        return this.GDI_DECKS.get(value);
      } else {
        return this.NOD_DECKS.get(value);
      }
    }
  }
}

export { DefaultDeckGenerator };
