// datatypes used in generator model

type Faction = "GDI" | "Nod";
type Building = "Infantry" | "Vehicle" | "Air" | "Tech" | "Commandar";

// this is a incomplete list
interface Unit {
  name: string;
  faction: Faction;
  building: Building;
  cost: number;
}

interface Deck {
  faction: Faction;
  commandar: Unit;
  units: Unit[];
}

// generation request
interface Request {
  faction: Faction | "random";
  name: string;
  validateFn: SerializedFunction<() => DeckValidationFunction>; // serialized function
  numDecks: number,
}

// generation response
interface Response {
  request: Request;
  result: Deck[];
  count: number;
}

class SerializedFunction<T extends Function> {
  readonly data: string;
  constructor(fn: T) {
    this.data = fn.toString();
  }

  deserialize(): T {
    return Function(`'use strict'; return (${this.data})`)();
  }

  static deserialize<U extends Function>(sf: SerializedFunction<U>): U {
    return Function(`'use strict'; return (${sf.data})`)();
  }
}

type DeckValidationFunction = (deck: Deck) => boolean;

// TODO: implement iterator
interface DeckDB /*extends Iterable<Deck>*/ {
  readonly units: Unit[];
  readonly commandars: Unit[];
  readonly size: number;
  get: (index: number) => Deck;
  getIndex: (deck: Deck) => number;
}

interface DeckGenerator {
  generate: (request: Request[]) => Response[];
}

export type { Faction, Building, Unit, Deck, DeckDB, DeckValidationFunction, Request, Response, DeckGenerator };
export { SerializedFunction };
