function isDefined(o: any) : boolean {
  return typeof o !== 'undefined';
}

// add an id field to an array of objects based on their index
// will NOT create/copy objects
// the returned array will be a new one (different from the original)
// but the content will be the same
function addId(arr: any[], key="iid"): any[] {
  return arr.map((d, idx) => {
    d[key] = idx;
    return d;
  });
}

