// n choose k
function choose(n: number, k: number): number {
  k = Math.min(n - k, k);
  let res = 1;
  for (let i = 1; i <= k; i++) {
    res *= n - k + i;
    res /= i;
  }
  return res;
}

// Select the 'index'th combination of k over n
function indexedCombination(n: number, k: number, index: number): number[] {
  let result = [];
  for (let item = 0; item < n + 1; item++) {
    const n_ = n - item;
    const pivot = choose(n_ - 1, k - 1);
    if (index < pivot) {
      result.push(item);
      k -= 1;
      if (k <= 0) {
        break
      }
    } else {
      index -= pivot;
    }
  }
  return result;
}

// Return the index of combination (length == k)
function combinationIndex(combination: number[], n: number): number {
  const k = combination.length;
  n -= 1;
  let index = 0;
  let item_in_check = 0;

  for (let i = 0; i < k; i++) {
    const offset = i + 1;
    const item = combination[i];
    while (item_in_check < item) {
      index += choose(n - item_in_check, k - offset);
      item_in_check += 1;
    }
    item_in_check += 1;
  }
  return index;
}

// reservoir-sampling, sample k data point from a stream of a unknown length
class StreamSampler<T> {
  private readonly k: number;
  private readonly randFn: () => number;
  private readonly r: T[];
  private i: number;
  private finished: boolean;

  constructor(k : number, randFn : () => number = Math.random) {
    this.k = k;
    this.randFn = randFn;
    this.r = [];
    this.i = 0;
    this.finished = false;
  }

  // call this function to add a new data point
  next(data: T): void {
    if (this.finished) {
      console.log("warning: call next after finalSample");
    }
    if (isDefined(data)) {
      if (this.r.length < this.k) {
        this.r.push(data);
      } else {
        // a random number between [0, i] inclusive
        const j = Math.floor(this.randFn() * (this.i + 1));
        if (j < this.k) {
          this.r[j] = data;
        }
      }
      this.i += 1;
    }
  }

  // call this function to read the current sample
  // note that the order of the sample will be based on the input order
  // DO NOT modify the returned array
  sample(): T[] {
    return this.r;
  }

  // call this function to read the final state
  // call next() after this will cause undefined behavior
  finalSample(): T[] {
    // TODO: shuffle the output
    shuffleArray(this.r);
    this.finished = true;
    return this.r;
  }
}

// shuffle array in-place
function shuffleArray(array: any[]): void {
  for (let i = array.length - 1; i > 0; i--) {
      const j = Math.floor(Math.random() * (i + 1));
      [array[i], array[j]] = [array[j], array[i]];
  }
}

